<template>
  <div id="page-batch-edit">

    <vs-alert color="danger" :title="$t('batch.notFound.title')" :active.sync="batch_not_found">
      <span>{{ $t('batch.notFound.message', { batchId: $route.params.batchId }) }} </span>
    </vs-alert>

    <vx-card v-if="batch_data">

      <div slot="no-body" class="tabs-container px-6 pt-6">

        <vs-tabs v-model="activeTab" class="tab-action-btn-fill-conatiner">
          <vs-tab :label="$t('batch.batch')" icon-pack="feather" icon="icon-batch">
            <div class="tab-text">
              <batch-edit-tab-account class="mt-4" :data="batch_data" />
            </div>
          </vs-tab>
        </vs-tabs>

      </div>

    </vx-card>
  </div>
</template>

<script>
import BatchEditTabAccount     from "./BatchEditTabAccount.vue"

// Store Module
import moduleBatchManagement from '@/store/batch-management/moduleBatchManagement.js'

export default {
  components: {
    BatchEditTabAccount
  },
  data() {
    return {
      batch_data: null,
      batch_not_found: false,

      /*
        This property is created for fetching latest data from API when tab is changed

        Please check it's watcher
      */
      activeTab: 0,
    }
  },
  watch: {
    activeTab() {
      this.fetch_batch_data(this.$route.params.batchId)
    }
  },
  methods: {
    fetch_batch_data(batchId) {
      this.$store.dispatch("batchManagement/fetchBatch", batchId)
        .then(res => { this.batch_data = res.data.attributes })
        .catch(err => {
          if(err.response.status === 404) {
            this.batch_not_found = true
            return
          }
          console.error(err) })
    }
  },
  created() {
    // Register Module BatchManagement Module
    if(!moduleBatchManagement.isRegistered) {
      this.$store.registerModule('batchManagement', moduleBatchManagement)
      moduleBatchManagement.isRegistered = true
    }
    this.fetch_batch_data(this.$route.params.batchId)
  }
}

</script>
